import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BaseViewDetailsComponent } from '../../../../pages/tzb/product-manage/base-product/base-product-view/base-view-details/base-view-details.component';
import { SaleDetailsComponent } from '../../../../pages/tzb/product-manage/available-product/sale-product-view/sale-view-details/sale-view-details.component';
import { BatchQueryDetailsComponent } from '../../../../pages/tzb/product-manage/product-market/product-launch/delivery-query/batch-query-details/batch-query-details.component';

export const routes: Routes = [
  {
    //批次号查询
    path: 'a/b/c/d/:applySerialNo/:status',
    component: BatchQueryDetailsComponent
  },
  {
    //基础产品
    path: 'basic/b/c/:baseProductId/:status',
    component: BaseViewDetailsComponent
  },
  {
    //可售产品
    path: 'sale/a/:baseProductId/:status',
    component: SaleDetailsComponent
  }
]

export const OtherMessageRouting: ModuleWithProviders = RouterModule.forChild(routes);
